@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@100..900&display=swap');

$wurd-header-bg-color: #350267;
$wurd-header-font-color: #fff;
$wurd-header-alt-font-color: #D5C6FF;
$wurd-font-family: "Lexend", sans-serif;

header {
    background-color: $wurd-header-bg-color;
    color: $wurd-header-font-color;
    font-family: $wurd-font-family!important;
    font-optical-sizing: auto;
    font-style:normal;
    font-weight: 300;  

    .wurd-header {
        padding: 20px 0;
    }

    img {       
        padding-right: 20px;
        // padding: top right bottom left
    }

    h1 {        
        font-family: $wurd-font-family!important;
        font-weight:500!important;
        border: none!important;
        margin: 0;
    }

    a {
        color: $wurd-header-font-color; 
        &:hover, &:focus, &:visited {
            color:$wurd-header-alt-font-color;
        }
    }

    #wb-bc li:before {
        color: $wurd-header-font-color;
    }

    
    
} 